main {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-align: center;
	    align-items: center;
	-ms-flex-pack: center;
	    justify-content: center;

	height: 100vh;
}

.chat-window {
	position: relative;

	display: -ms-flexbox;

	display: flex;
	overflow: hidden;
	-ms-flex-direction: column;
	    flex-direction: column;

	height: 100%;
	min-height: 18.75rem;
	max-height: 31.25rem;
	margin: 1rem;

	border-radius: .5rem;
	background: #f3f3f3;
	-webkit-box-shadow: 0 .625rem 1.875rem rgba(0, 0, 0, .2);
	        box-shadow: 0 .625rem 1.875rem rgba(0, 0, 0, .2);
}

header {
	position: absolute;
	z-index: 100;
	top: 0;
	right: 0;
	left: 0;
}



